.converter-container{
    display: flex;
    width: 30vw;
    flex-direction: column;
    align-items: center;
    padding: 4rem;
    border-radius: 1rem;
    background: rgb(131,58,180);
}
p{
    padding: 1rem 0;
    font-family: "Cairo",sans-serif;
}
option{
    background: rgb(62, 127, 225);
    text-align: center;

}

select::-webkit-scrollbar{
    display: none;
}

button{
    width: 50%;
    margin: 1rem 0;
    background: #CFE352;
    padding: 0.5rem;
    border:none;
    border-radius:1rem;
}
input{
    font-size: 1.5rem;
    font-family: "Cairo",sans-serif;
    border:none;
    border-radius:2rem;
    text-align: center;
}